<template>
  <div class="login-page">
    <!-- <langselect class="lang" /> -->
    <!-- <el-tooltip class="svg-github" effect="dark" content="Fork Me" placement="bottom">
      <a href="https://github.com/Sakuyakun/collarTicket"><icon name="github" :scale="2.5"></icon></a>
    </el-tooltip> -->
    
    <div class="login-wrap">
      <el-col :class="translateLeft" :span="10">

        <div>
          <div class="logo">
            <!-- <icon name="tree" :scale="8"></icon> -->
            <div class="title">
              <!-- <a>
                <span>{{$t('login.edenPart1')}}</span><span class="subtitle">{{$t('login.edenPart2')}}</span>
              </a> -->
              <a>
                CollarTicket
              </a>
            </div>
          </div>
        
          <div class="login-form">
            <!-- <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
              <el-form-item prop="username">
                <el-input :placeholder="$t('login.userplaceholder')" v-model="ruleForm.username"></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input :placeholder="$t('login.pwdplaceholder')" type="password" v-model="ruleForm.password"></el-input>
              </el-form-item>
              <el-form-item class="btn">
                <el-button :loading="loading" type="primary" @click="handleLogin('ruleForm')">{{$t('login.btn')}}</el-button>
              </el-form-item>
            </el-form> -->
            <el-form label-width="70px" :model="ruleForm" :rules="rules" ref="ruleForm">
              <el-form-item label="手机号" prop="username">
                <el-input v-model="ruleForm.username"></el-input>
              </el-form-item>
              <el-form-item label="验证码" prop="password" class="codeInp">
                <el-col :span="10">
                  <el-input v-model="ruleForm.password" class="pswInp"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item class="codeRecord">
                <el-button type="primary" class="codeBtn" @click="getCode()" ref="getcode">获取验证码</el-button>
              </el-form-item>
              <el-form-item class="btn">
                <el-button :loading="loading" type="primary" @click="handleLogin('ruleForm')">确定</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>

      </el-col>

      <el-col :class="translateRight" :span="14">
        <div class="wallpaper"></div>
      </el-col>
    </div>
  </div>
</template>

<script>
import langselect from '@/components/langselect'
import storage from '@/utils/storage'

// 验证码插件
import {resend} from '@/code/code.js'

// 引入接口
import api from '@/api/api.js'

const useRegexp = {
  exist: /\S+/
}

export default {
  name: 'login',
  components: {
    langselect
  },
  // mounted() {
  //   this.$notify({
  //     title: '登陆提示',
  //     message: '用户名 admin 密码随意输入',
  //     position: 'top-left',
  //     duration: 0
  //   })
  // },
  data() {
    const validobj = {
      username: [
        { ruleName: 'exist',error: '请输入手机号' }
      ],
      password: [{ ruleName: 'exist', error: '请输入验证码' }]
    }

    const _typeof = val =>
      Object.prototype.toString
        .call(val)
        .replace(/^\S+\s/, '')
        .replace(/]$/, '')
        .toLocaleLowerCase()

    const validfn = (rule, value, callback) => {
      const _validobj = validobj[rule.field.replace(/^\S+(?=\.)\./g, '')]
      for (let i = 0; i < _validobj.length; i++) {
        let _rule = useRegexp[_validobj[i].ruleName]
        if (_typeof(_rule) === 'regexp') {
          if (!_rule.test(value)) {
            return callback(new Error(_validobj[i].error))
          }
        } else if (_typeof(_rule) === 'function') {
          if (_rule(value, _validobj[i].params)) {
            return callback(new Error(_validobj[i].error))
          }
        }
      }
      callback()
    }
    // 手机验证
    var checkPhone = (rule, value, callback) => {        
      if (!value) {           
        return callback(new Error('手机号不能为空'));         
      } else {          
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        console.log(reg.test(value));          
        if (reg.test(value)) {            
          callback();         
        } else {            
          return callback(new Error('请输入正确的手机号'));          
        }       
    }};

    // 验证码验证
    var checkCode = (rule, value, callback) => {        
      if (!value) {           
        return callback(new Error('验证码不能为空'));         
      } else {          
        const reg = /^\d{6}$/;
        console.log(reg.test(value));          
        if (reg.test(value)) {            
          callback();         
        } else {            
          return callback(new Error('请输入正确的验证码'));          
        }       
    }};


    return {
      lang: this.$store.state.app.language,
      ruleForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ validator: checkPhone, trigger: 'blur', required: true }],
        password: [{ validator: checkCode, trigger: 'blur', required: true }]
      },
      remember: true,
      loading: false,
      switchLeft: false,
      switchRight: false,
      notforget: true,
      forgetForm: {
        email: '',
        newPassword: '',
        confirmPassword: ''
      }
    }
  },
  computed: {
    translateLeft() {
      return {
        'translate-left': true,
        'login-col': true,
        'switch-left': this.switchLeft
      }
    },
    translateRight() {
      return {
        'translate-right': true,
        'login-col': true,
        'switch-right': this.switchLeft
      }
    }
  },
  methods: {
    wrapSwitch(state) {
      this.switchLeft = !this.switchLeft
      this.switchRight = !this.switchRight
      setTimeout(() => {
        this.notforget = state
        this.$refs['ruleForm'].resetFields()
        // this.$refs['forgetRuleForm'].resetFields()
      }, 300)
    },
    handleLogin(formName) {
      this.loading = true
      this.$refs[formName].validate(async valid => {
        if (valid) {
          // 验证输入的手机号与验证码
          // console.log(22);
            var that = this;
            var xhr = new XMLHttpRequest();
            xhr.open('post',`${api.dev}/admin/checkUser`,true);
            xhr.setRequestHeader('Content-Type','application/x-www-form-urlencoded');
            xhr.send('phone=' + this.ruleForm.username +'&'+ 'code=' + this.ruleForm.password);
            xhr.onreadystatechange = function () {
              if(xhr.status == 200 && xhr.readyState == 4) {
                var res = JSON.parse(xhr.responseText);
                console.log(res);
                if(res.code == 200) {
                  
                  that.$message({
                    message: res.msg,
                    type: 'success',
                    duration: 1000
                  });

                  let username = 'admin';
                  let {password} = that.ruleForm;
                  // this.remember
                  //   ? storage.set('loginUser', username)
                  //   : storage.remove('loginUser', username)

                  const response = that.$store.dispatch('loginbyUser', {
                    username: username.trim(),
                    password: password
                  })
                
                  // this.loading = false

                  that.$notify.closeAll()
                  that.$router.push({ path: '/' })
                  
                  setTimeout(() => {
                    window.location.href = '/collarticket'
                  }, 1500);

                }else if(res.code == 500) {
                  that.$message.error(res.msg);
                  that.loading = false;
                  // console.log(res.msg)
                  // return;
                  // alert(res.msg);
                }
              }
            }
            // let { username, password } = this.ruleForm
            // let username = 'admin';
            // let {password} = this.ruleForm;
            // // this.remember
            // //   ? storage.set('loginUser', username)
            // //   : storage.remove('loginUser', username)
            // const response = await this.$store.dispatch('loginbyUser', {
            //   username: username.trim(),
            //   password: password
            // })
            // // this.loading = false

            // this.$notify.closeAll()
            // this.$router.push({ path: '/' })
            
            // if (response.data) {
            //       this.$notify.closeAll()
            //       this.$router.push({ path: '/' })
            //     } else {
            //       this.$message({
            //         message: 'Bad World',
            //         type: 'error',
            //         duration: 10000,
            //         showClose: true
            //       })
            //   }
            
            // if(this.ruleForm.username == 18814378294 && this.ruleForm.password == 111111) {
            //     if (response.data) {
            //       this.$notify.closeAll()
            //       this.$router.push({ path: '/' })
            //     } else {
            //       this.$message({
            //         message: 'Bad World',
            //         type: 'error',
            //         duration: 10000,
            //         showClose: true
            //       })
            //   }
            // }

            // this.$http.post(`${api.dev}/admin/checkUser`,{phone:this.ruleForm.username,code:this.ruleForm.password},{emulateJSON:true}).then(res => {
            //   if(res.code == 200) {
            //     if (response.data) {
            //         this.$notify.closeAll()
            //         this.$router.push({ path: '/' })
            //     } else {
            //         this.$message({
            //           message: 'Bad World',
            //           type: 'error',
            //           duration: 10000,
            //           showClose: true
            //         })
            //     }
            //   }else if(res.code == 500) {
            //     // this.$message.error(res.msg);
            //     console.log(res.msg);
            //   }
            // })
          
        } else {
          this.loading = false
          this.$message.error(this.$t('login.validfaild'))
        }
      })
    },
    forgetHandle() {
      this.$message.success(this.$t('login.pwdChanged'))
      this.wrapSwitch(true)
    },
    getCode() {
      // console.log(123);
      if(this.ruleForm.username != '' && this.ruleForm.username.length == 11) {
        this.$http.post(`${api.dev}/user/sendSmsCode`,{phone : this.ruleForm.username} , {emulateJSON:true}).then(res => {
          console.log(res);
        })
        resend(this.$refs.getcode.$el)
      }

      // console.log(this.ruleForm.username)
      // console.log(this.ruleForm.password)
    }
  }
}
</script>

<style lang="stylus">
.forget-form,
.login-form
  .el-form-item__content
    line-height 40px
  .el-input__inner
    padding-top: 2px;
    height 40px
    line-height 40px

.btn button
    width: 100%
    padding 12px 20px
</style>

<style lang="stylus" scoped>
@import "../../assets/styl/variables.styl"

.login-col
  height 100%

.login-page
  display flex
  justify-content center
  align-items center
  position absolute
  height 100%
  width 100%
  .lang
    position absolute
    right 59px
    top 24px
  .svg-github
    position absolute
    right 29px
    top 25px
  .translate-left
  .translate-right
    will-change auto
    transform translateX(0px)
    transition transform .6s ease-in-out
  .switch-left
    transform translateX(525px)
  .switch-right
    transform translateX(-375px)
  
.login-wrap
  overflow hidden
  width 900px
  height 400px
  background white
  border-radius 4px
  transform translateY(-10px)
  box-shadow 0 1px 3px 0 rgba(0, 0, 0, .12), 0 0 3px 0 rgba(0, 0, 0, .04)

  .logo
    padding-top 26px
    text-align center

  .title
    font-weight bold
    color main-color
    padding-top 8px
    font-size 22px

    a
      cursor cell
    a:before
      content '['
      opacity 0
      margin-right 10px
      transform translateX(-10px)
      transition transform .2s, opacity .2s

    a:after
      content ']'
      opacity 0
      margin-left 10px
      transform translateX(10px)
      transition transform .2s, opacity .2s

    a:hover:before
    a:hover:after
      opacity 1
      transform translateX(0)

    .subtitle
      color sub-color

  .forgetwrap-title
    padding-top 30px
    padding-left 15px

  .forget-form
    padding 20px 30px 30px
    padding-bottom 0

  .login-form
    padding 30px
    padding-bottom 0

  .login-footer
    padding 0 30px 
    .forgetpwd
      text-align right 
      span
        cursor pointer
        font-size 14px
        font-weight 500
        color #606266

  .wallpaper
    width 100%
    height 100%
    background url('../../assets/images/loginwallpaper.jpg')
    background-size cover
    position relative
  
  .codeRecord
    margin-bottom 0
    .codeBtn
      float right 
      margin-top -62px
</style>
